        }
      }
    }
    /deep/ {
      .button-container {
        margin-bottom: 10px;
      }

      /*修改表格单元格高度*/
      .bda-pagination-table {
        .bda-pagination-table-page {
          border: 0 !important;
          height: 50px !important;
        }
        .bda-pagination-table-main {
          height: calc(100% - 40px);
        }
      }
      .el-form-item__label {
        color: #7f7f7f;
      }
      /*checkbox样式*/

      .el-checkbox__input.is-disabled {
        width: 100%;
        padding: 0 auto;
      }

      .el-checkbox__input.is-disabled .el-checkbox__inner,
      .el-checkbox__input .el-checkbox__inner {
        background-color: #fff !important;
        width: 16px;
        height: 16px;
        border: 1px solid #bbb;
        border-radius: 2px;
      }
      .el-checkbox__inner:after{
        border-color: #333;
      }

      /*解决调整单元格高度后，fixed错位*/
      .el-table__fixed-body-wrapper {
        top: 37px !important;
      }

      /*解决表格滚动高度固定导致的问题*/
      .el-table__body-wrapper.is-scrolling-left {
        height: calc(100% - 48px) !important;
      }
      .el-table__body-wrapper.is-scrolling-right {
        height: calc(100% - 48px) !important;
      }
      .el-table__body-wrapper.is-scrolling-middle {
        height: calc(100% - 48px) !important;
      }
      .el-table__fixed-body-wrapper {
        height: calc(100% - 34px) !important;
      }

      /*解决调整列宽度时，出现列内容未完整显示的问题*/
      .el-table .cell.el-tooltip {
        width: 100% !important;
      }

      .el-table__header-wrapper {
        background-color: #22519c;
      }

      .el-table__body-wrapper {
        background-color: #fff;
      }

      .el-table__header th {
        // background-color: #22519c !important;
        background: rgba(188, 228, 249, 0.4)!important;
        height: 48px;
        border-color: #bbb;

        .cell {
          color: #666 !important;
          text-align: center;
        }
      }

      .bda-table-main {
        background-color: #071a47;
      }

      .bda-main {
        /*height: calc(100% - 52px);*/
        border: 1px solid #bbb;
        .el-table__row {
          &:hover > td {
            background-color: #ebfcff !important;
          }
          > td {
            height: 48px;
            border-color: #bbb;

            .cell {
              color: #333;
              text-align: center;
              .el-button {
                color: #66b9ff;
